
@import url('https://fonts.googleapis.com/css?family=BioRhyme|Bungee');
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background-image: url("https://wallpapertag.com/wallpaper/full/0/1/3/128024-pixel-landscape-background-tumblr-1920x1080-for-samsung-galaxy.jpg")
}

h1 {
  font-family: 'Bungee',
  cursive;
  font-size: 8rem;
  text-align: center;
  padding: 2rem;
  background-color: lightcoral;
  border: 1rem solid rgba(255, 197, 4, 0.94);
  display: inline;
}

header {
  text-align: center;
  margin-top: 4rem;
}

.party-finder {
  margin: 7rem auto 0 auto;
  width: 50%;
  padding: 5rem;
  background-color: rgba(230, 172, 71, 0.495);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  border: 0.3rem dotted rgba(237, 183, 8, 0.831);

}

.party-finder form {
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction: column;
      flex-direction: column;
  margin: 2rem;
  width: 40%;
}


.party-list {
  width: 100%;
  height: 100%;
  -webkit-transition: all 2s ease;
  -o-transition: all 2s ease;
  transition: all 2s ease;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
  -ms-flex-align: center;
      align-items: center;
}

.hero-card {
  height: 12rem;
  display: -ms-flexbox;
  display: flex;
  padding: 1rem;
  text-align: center;
  font-family: 'BioRhyme',
  serif;
  -ms-flex-pack: end;
      justify-content: flex-end;
  -ms-flex-direction: column;
      flex-direction: column;
  background-color: rgba(241, 182, 32, 0.824);
  border: 3px solid salmon;
}

.hero-inv {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-item-align: end;
      align-self: flex-end;
  width: 100%;
  padding:0.2rem;
}
.hero-inv img {
  background-color: lightgray;
  border: 0.1rem solid rgba(241, 182, 32, 0.824);
  padding: 0.2rem;
  border-radius: 50%;
  height: auto;
  width: auto;
}

button {
  font-family: 'BioRhyme',
  serif;
  color: white;
  background-color: rgb(205, 149, 7);
  border: 1px solid rgb(154, 0, 154);
  -webkit-transition: all 0.4s ease;
  -o-transition: all 0.4s ease;
  transition: all 0.4s ease;
}

button:hover {
  border: 0.1rem solid rgba(241, 182, 32, 0.824);
  background-color: #EBD292;
}

input {
  display: block;
  margin-bottom: 1rem;
  padding: 5px;
  border: 4px solid #F1B720;
  border-radius: 5px;
  color: #333;
  -webkit-transition: all 0.3s ease-out;
  -o-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
}

input:hover {
  border-radius: 8px;
}

input:focus {
  outline: none;
  border-radius: 8px;
  border-color: #EBD292;
}


